  <div class="home">
    <div class="bg" >
      <img :src="bg"/>
    </div>
    <div class="content">
      <div class="video">
          <div class="light"></div>
        <img src="../assets/bg_radio@3x.png"/>
        <div class="bar">
          <img class="bicon" src="../assets/img_screen@3x.png"/>
          <div id="jdt" :style="{width: jdt +'%'}" class="jdt"></div>
          <span class="left">{{music_count}}</span>
          <span class="right" >{{end_time}}</span>
        </div>
        <div class="music">
          <img class="zicon" @click="getDataFromApi(12345,1)" src="../assets/btn_previous@3x.png"/>
          <Music  :offPath="offPath" :onPath="onPath" :musicPath="musicPath"/>
          <img class="zicon" @click="getDataFromApi(12345,2)" src="../assets/btn_next@3x.png"/>
        </div>
      </div>
      <div class="title">{{title_w}}</div>
      <div class="desc">
        <div class="left">
          <div class="zls">
            故事 &nbsp;|&nbsp; {{zb}}
          </div>
          <div class="zls">
            声音 &nbsp;|&nbsp; {{gs}}
          </div>
          <div class="zls">
            背景音乐 &nbsp;|&nbsp; {{source}}
          </div>
        </div>
        <div class="right">
          <div class="number">Vol.{{number}}</div>
