  <div id="app">
    <router-view/>
    <div class="bottom">
      <a   target="view_window" href="https://a.app.qq.com/o/simple.jsp?pkgname=com.psyone.brainmusic" >
      <div class="bottom-img">
        <img  class='bottom-hehe' src="./assets/a.png"/>
