      <div class="warn-open-outer" v-if="show">
        <div class="warn">
          <p class="title">试听结束</p>
          <p class="center">你的试听已结束，赶紧前往小睡眠App体验更多音频吧 (～￣▽￣)～</p>
          <p @click="aa" class="warn-open">打开小睡眠App</p>
          <img @click="hehe" class="close" src="./assets/btn_close@3x.png"/>
        </div>
      </div>

    </transition>
    <transition name="fade">
      <div v-if="tips_toggle" class="tips">请点击右上角菜单使用浏览器打开</div>
    </transition>
    <transition name="fade">
      <div v-if="tips_toggle2" class="tips">已经没有啦~</div>
    </transition>
  </div>
